/* Шрифты */
@font-face {
    font-family: "OpenSans";
    src: url('../fonts/OpenSansRegular.eot'), url("../fonts/OpenSansRegular.woff"), url("../fonts/OpenSansRegular.ttf");
    font-weight: 300;
}
@font-face {
    font-family: "OpenSans";
    src: url('../fonts/OpenSansBold.eot'), url("../fonts/OpenSansBold.woff"), url("../fonts/OpenSansBold.ttf");
    font-weight: 600;
}
@font-face {
    font-family: "Oswald";
    src: url('../fonts/Oswald-Light.eot'), url("../fonts/Oswald-Light.woff"), url("../fonts/Oswald-Light.ttf");
    font-weight: 200;
}
@font-face {
    font-family: "Oswald";
    src: url('../fonts/Oswald-Regular.eot'), url("../fonts/Oswald-Regular.woff"), url("../fonts/Oswald-Regular.ttf");
    font-weight: 400;
}
@font-face {
    font-family: "Oswald";
    src: url('../fonts/Oswald-Medium.eot'), url("../fonts/Oswald-Medium.woff"), url("../fonts/Oswald-Medium.ttf");
    font-weight: 600;
}

/*общие стили*/
* {
    box-sizing: border-box;
}
body {
    font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji";
}
#root {
    overflow: hidden;
}
a,
p,
li {
    font-family: "OpenSans", sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.3;
    color: #333;
}
p {
    margin-bottom: 20px;
}
a {
    text-decoration: none;
    color: #333;
}
a:focus {
    outline: unset;
}
h1,
h2 {
    font-family: "Oswald", sans-serif;
    color: #333;
    font-weight: 400;
    line-height: 1.3;
    margin-bottom: 25px;
}
h1 {
    font-size: 90px;
}
h2 {
    font-size: 75px;
}
h3,
h4 {
    font-family: "Oswald", sans-serif;
    font-weight: 200;
    line-height: 1.3;
    margin-bottom: 15px;
}
h3 {
    font-size: 35px;
}
h4 {
    font-size: 28px;
}
strong,
b {
    font-weight: 600;
}
mark {
    background-color: unset;
}
img {
    max-width: 100%;
}
hr {
    margin: 20px 0;
    border-width: 0;
    border-top: 1px solid #e0dede;
}
@media only screen and (max-width: 1020px) {
    h1 {
        font-size: 75px;
    }
    h2 {
        font-size: 68px;
    }
    h3 {
        font-size: 28px;
    }
    h4 {
        font-size: 24px;
    }
}
@media only screen and (max-width: 800px) {
    h1 {
        font-size: 68px;
    }
    h2 {
        font-size: 58px;
    }
    h3 {
        font-size: 26px;
    }
    h4 {
        font-size: 22px;
    }
}
@media only screen and (max-width: 500px) {
    h1 {
        font-size: 50px;
    }
    h2 {
        font-size: 40px;
    }
    h3 {
        font-size: 24px;
    }
    h4 {
        font-size: 20px;
    }
}

/* Основная ширина */
.container {
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
}

/* стили для ul li */
ul.ul-custom {
    margin-bottom: 20px;
}
ul.ul-custom li {
    position: relative;
    margin-bottom: 3px;
    padding-left: 24px;
    line-height: 1.3;
}
ul.ul-custom li:before {
    content: "";
    position: absolute;
    top: 9px;
    left: 8px;
    width: 6px;
    height: 6px;
    background: #29323C;
    border-radius: 50%;
}